@font-face {
  font-family: 'Morison';
  src: url(./assets/fonts/Morison-Display.woff2);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Spartan';
  src: url(./assets/fonts/Spartan-Thin.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Noto Sans JP';
  src: url(./assets/fonts/NotoSansJP-Thin.otf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Noto Serif JP';
  src: url(./assets/fonts/NotoSerifJP-ExtraLight.otf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.ja {
  font-family: 'Noto Serif JP', serif;
}
body {
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #4d4948;
  overflow-y: scroll;
  /* Show vertical scrollbar */
}
a {
  text-decoration: none;
  color: inherit;
}
h1,
.h1,
h2,
.h2 {
  font-family: 'Morison', sans-serif;
  font-weight: normal;
  margin-bottom: 10px;
}
h2,
.h2 {
  font-size: 2rem;
  margin: 10px 0;
  display: inline-block;
}
::selection {
  background: rgba(135, 206, 250, 0.2);
  /* WebKit/Blink Browsers */
}
.router-link {
  text-decoration: none;
  color: #4d4948;
}
.main-header {
  display: grid;
  grid-template-areas: 'a';
}
.main-header .main-header_image {
  grid-area: a;
}
@media (min-width: 1025px) {
  .instagram-modal_text::-webkit-scrollbar,
  body::-webkit-scrollbar {
    width: 11px;
  }
  .instagram-modal_text,
  body {
    scrollbar-width: thin;
    scrollbar-color: rgba(135, 206, 250, 0.2) none;
  }
  .instagram-modal_text::-webkit-scrollbar-track,
  body::-webkit-scrollbar-track {
    background: none;
  }
  .instagram-modal_text::-webkit-scrollbar-thumb,
  body::-webkit-scrollbar-thumb {
    background-color: rgba(135, 206, 250, 0.45);
    border-radius: 6px;
  }
}
/*******************/
/* SITE COMPONENTS */
/*******************/
.nanagogo,
.blog,
.gallery,
.media,
.profile,
.schedule,
.works {
  margin-top: 90px;
  min-height: calc(100vh - 90px - 50px - 40px - 40px);
}
.container {
  max-width: 940px;
  margin: 0 15px;
  padding: 30px 0;
}
@media (min-width: 1025px) {
  .container {
    margin: 0 auto;
  }
}
.container.container--xs {
  max-width: 769px;
}
.container .headline {
  margin-top: 40px;
  margin-bottom: 15px;
  animation: 1.5s slide-in ease;
}
.container .headline.headline--left .headline_title {
  padding-left: 6px;
  border-left: 3px solid rgba(135, 206, 250, 0.2);
}
.container .headline.headline--center {
  text-align: center;
}
.container .headline.headline--center .headline_title {
  border-bottom: 3px solid rgba(135, 206, 250, 0.2);
}
.container .headline.headline--bottom-border {
  border-bottom: 1px solid #4d494885;
}
.nav-home {
  font-family: 'Morison', sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  z-index: 999;
}
.nav-home.navigation--bottom {
  position: absolute;
  display: flex;
  width: 100%;
  top: calc(90vh - 50px);
}
@media (min-width: 481px) {
  .nav-home.navigation--bottom {
    top: 90vh;
  }
}
.nav-home .router-link {
  padding: 13px 15px 0;
}
.nav-home .router-link:after {
  display: block;
  content: '';
  border-bottom: solid 3px rgba(135, 206, 250, 0.2);
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
  margin: 10px auto 0;
}
.nav-home .router-link:hover:after {
  transform: scaleX(1);
}
.nav {
  font-family: 'Morison', sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.9);
  transition: all 0.5s ease-in-out;
  z-index: 999;
}
.nav.nav--background-transparent {
  background-color: transparent;
}
.nav .nav-top {
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav .nav-top .nav-home-link {
  font-family: 'Noto Sans JP', sans-serif;
}
.nav .nav-top .nav-home-link .nav-home-link_title {
  font-weight: bold;
  font-size: 2rem;
  line-height: 1.2;
}
.nav .nav-top .nav-home-link .nav-home-link_subtitle {
  font-size: 0.7rem;
  letter-spacing: 0.1rem;
}
.nav .nav-top .navigation-toggle {
  display: flex;
  flex-direction: column;
  height: 20px;
  justify-content: space-between;
  align-items: flex-end;
  transition: all 1s;
  z-index: 999;
  opacity: 1;
  cursor: pointer;
}
@media (min-width: 1025px) {
  .nav .nav-top .navigation-toggle {
    display: none;
  }
}
.nav .nav-top .navigation-toggle .navigation-toggle-bar-1,
.nav .nav-top .navigation-toggle .navigation-toggle-bar-2,
.nav .nav-top .navigation-toggle .navigation-toggle-bar-3 {
  border-bottom: 2px solid #4d4948;
  transition: 0.5s ease-in-out;
}
.nav .nav-top .navigation-toggle .navigation-toggle-bar-1 {
  width: 30px;
}
.nav .nav-top .navigation-toggle .navigation-toggle-bar-1.toggled {
  transform-origin: right;
  transform: rotate(-45deg);
  width: 25px;
}
.nav .nav-top .navigation-toggle .navigation-toggle-bar-2 {
  width: 20px;
}
.nav .nav-top .navigation-toggle .navigation-toggle-bar-2.toggled {
  width: 0;
}
.nav .nav-top .navigation-toggle .navigation-toggle-bar-3 {
  width: 20px;
}
.nav .nav-top .navigation-toggle .navigation-toggle-bar-3.toggled {
  transform-origin: right;
  transform: rotate(45deg);
  width: 25px;
}
.nav .nav-top .nav-main {
  display: none;
}
@media (min-width: 1025px) {
  .nav .nav-top .nav-main {
    display: flex;
  }
}
.nav .nav-top .nav-main .router-link {
  padding: 0 20px;
  margin-top: 10px;
}
.nav .nav-top .nav-main .router-link:after {
  display: block;
  content: '';
  border-bottom: solid 3px rgba(135, 206, 250, 0.2);
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
  margin: 10px auto 0;
}
.nav .nav-top .nav-main .router-link:hover:after {
  transform: scaleX(1);
}
.nav .nav-top .nav-main .router-link.router-link-active.router-link-exact-active:after {
  transform: scaleX(1);
}
.nav .nav-side {
  font-family: 'Spartan', sans-serif;
  font-size: 1.5rem;
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.5s ease-in-out;
  opacity: 0;
  visibility: hidden;
}
.nav .nav-side.navigation-side--active {
  opacity: 1;
  visibility: visible;
}
.nav .nav-side .router-link {
  padding: 13px 15px 0;
  line-height: 2;
  font-weight: 100;
}
.schedule-card {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
  grid-gap: 25px;
  border-right: 3px solid rgba(135, 206, 250, 0.2);
  margin: 15px 0;
}
.schedule-card .schedule-card_left {
  display: grid;
  justify-content: center;
  align-items: center;
  justify-items: center;
  grid-gap: 5px;
  padding: 20px;
  background-color: rgba(135, 206, 250, 0.15);
}
.schedule-card .schedule-card_left .schedule-card_date {
  font-size: 2.5rem;
  line-height: 1;
}
.schedule-card .schedule-card_left .schedule-card_weekday {
  font-size: 1.5rem;
  line-height: 1;
}
.schedule-card .schedule-card_left .schedule-card_month-year {
  font-size: 0.8rem;
}
.schedule-card .schedule-card_right .schedule-card_type {
  border: 1px solid #4d4948;
  display: inline-block;
  padding: 0 5px;
  font-size: 12px;
  margin: 10px 0;
  border-radius: 4px;
}
.profile-card {
  display: grid;
  grid-gap: 10px;
  justify-items: center;
}
@media (min-width: 1025px) {
  .profile-card {
    grid-template-columns: 1fr 1fr;
  }
}
.profile-card .profile-card_name {
  text-align: center;
}
.profile-card .profile-card_image {
  width: 80%;
  max-width: 300px;
}
.profile-card .profile-card_content {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
.blog-card {
  display: grid;
  grid-template-columns: 100px 1fr;
  grid-gap: 10px;
  margin: 10px 0;
}
.blog-card .blog-card_image {
  height: 100px;
  width: 100px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-items: center;
}
.blog-card .blog-card_image .base-image {
  height: 100%;
}
.blog-card .blog-card_content {
  height: 100px;
}
.blog-card .blog-card_date.blog-card_date--placeholder {
  width: 50%;
  height: 1em;
}
.blog-card .blog-card_title,
.blog-card .blog-card_text {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  word-break: break-all;
}
.blog-card .blog-card_title {
  -webkit-line-clamp: 1;
  font-weight: bolder;
}
.blog-card .blog-card_title.blog-card_title--placeholder {
  width: 75%;
  height: 1em;
}
.blog-card .blog-card_text {
  -webkit-line-clamp: 2;
}
.blog-card .blog-card_text.blog-card_text--placeholder {
  width: 100%;
  height: 3em;
}
.blog-card .blog-card_date--placeholder,
.blog-card .blog-card_title--placeholder,
.blog-card .blog-card_text--placeholder {
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.05);
  margin: 3px 0;
}
.blog-card .blog-card_hr {
  grid-column: 1/3;
  width: 75%;
  max-width: 500px;
  border: none;
  border-bottom: 1px solid #4d494885;
}
.gallery-image-container {
  column-count: 2;
  column-gap: 5px;
}
@media (min-width: 481px) {
  .gallery-image-container {
    column-count: 3;
  }
}
@media (min-width: 1025px) {
  .gallery-image-container {
    column-count: 4;
  }
}
.gallery-image-container.gallery-image-container--instagram {
  display: grid;
  /* grid-template-columns: 1fr 1fr 1fr 1fr; */
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 481px) {
  .gallery-image-container.gallery-image-container--instagram {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 1025px) {
  .gallery-image-container.gallery-image-container--instagram {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
.gallery-image-container .base-image {
  /* Just in case there are inline attributes */
  width: 100%;
  height: auto;
}
.instagram-video-container,
.instagram-video,
.instagram-image {
  width: 100%;
}
.gogo-card {
  display: grid;
  grid-template-columns: min-content 1fr;
}
.gogo-card .gogo-card_profile {
  width: 50px;
  border-radius: 50%;
}
.gogo-card .gogo-card_text {
  max-width: 350px;
  background-color: rgba(135, 206, 250, 0.2);
  border-radius: 0 10px 10px 10px;
  padding: 15px;
  margin: 15px;
  word-break: break-all;
}
.gogo-card.gogo-card--other {
  grid-template-columns: 1fr min-content;
  justify-items: end;
}
.gogo-card.gogo-card--other .gogo-card_text {
  order: 1;
  background-color: lemonchiffon;
  border-radius: 10px 0 10px 10px;
}
.gogo-card.gogo-card--other .gogo-card_profile {
  order: 2;
}
.work-card {
  display: grid;
  grid-template-columns: 85px 1fr;
  grid-gap: 10px;
  margin: 20px 0;
}
.work-card .work-card_title {
  border-right: 1px solid rgba(135, 206, 250, 0.45);
  font-family: 'Morison', sans-serif;
  font-weight: bold;
}
.work-card .word-card_entry {
  margin-bottom: 10px;
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: start;
  align-content: start;
  grid-gap: 5px;
}
.work-card .word-card_entry .bullet_point {
  opacity: 0.4;
  height: 100%;
}
.work-card .word-card_entry .entry-romaji,
.work-card .word-card_entry .entry-kanji,
.work-card .word-card_entry .entry-year {
  grid-column-start: 2;
}
/*******************/
/* BASE COMPONENTS */
/*******************/
.base-image {
  width: 100%;
  height: auto;
  transition: all 0.5s ease-in;
  overflow: hidden;
}
.base-image.base-image--square {
  aspect-ratio: 1;
  object-fit: cover;
}
.more-button {
  width: 100%;
  display: flex;
  align-content: center;
  justify-content: center;
  margin-top: 30px;
}
.more-button .more-button_text {
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  color: #4d4948;
  font-family: 'Spartan', sans-serif;
  text-decoration: none;
  border-bottom: 1px solid #4d494885;
}
.more-button .more-button_text:hover {
  border-color: rgba(135, 206, 250, 0.2);
}
.input_group {
  position: relative;
  padding-top: 15px;
  margin-bottom: 30px;
}
.input_group .input_text-container {
  display: grid;
  grid-template-columns: 1fr min-content;
}
.input_group .input_delete,
.input_group .input_field {
  font-family: "Noto Sans JP", sans-serif;
  width: 100%;
  border: 0;
  border-bottom: 1px solid #4d494885;
  color: #4d4948;
  outline: 0;
  font-size: 0.9rem;
  padding: 10px 0 7px 0;
  background: transparent;
  transition: all 0.35s ease;
}
.input_group .input_delete::placeholder,
.input_group .input_field::placeholder {
  color: transparent;
}
.input_group .input_delete:placeholder-shown ~ .input_label,
.input_group .input_field:placeholder-shown ~ .input_label {
  font-size: 1rem;
  cursor: text;
  top: 20px;
}
.input_group .input_delete:focus,
.input_group .input_field:focus {
  padding-bottom: 7px;
  border-color: rgba(135, 206, 250, 0.45);
}
.input_group .input_delete:focus ~ .input_label,
.input_group .input_field:focus ~ .input_label {
  position: absolute;
  top: 0;
  display: block;
  transition: all 0.35s ease;
  font-size: 1rem;
  color: #4d4948;
}
.input_group .input_delete:focus + .input_delete,
.input_group .input_field:focus + .input_delete {
  border-color: rgba(135, 206, 250, 0.45);
}
.input_group .input_delete {
  transform: scaleX(1.5);
}
.input_group .input_label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.35s;
  font-size: 1rem;
  user-select: none;
  color: #4d4948;
}
.video-container {
  position: relative;
  width: 100%;
}
.video-container.video-container--iframe {
  height: 0;
  padding-bottom: 56.25%;
  margin-bottom: 30px;
}
.video-container .instagram-video-container {
  position: absolute;
}
.video-container .play-button {
  color: white;
  position: absolute;
  left: 5px;
  top: 5px;
  z-index: 1;
}
.video-container .video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.base-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.6);
}
.base-modal .close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 2em;
  color: white;
  cursor: pointer;
}
.base-modal .modal-container {
  margin: 0 auto;
  position: relative;
  top: 10%;
  max-width: 90%;
  max-height: 85vh;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.base-modal .modal-container .instagram-modal {
  display: grid;
  grid-template-rows: max-content 1fr;
  height: 85vh;
  max-width: 940px;
}
@media (min-width: 481px) {
  .base-modal .modal-container .instagram-modal {
    grid-template-columns: 65% 1fr;
    grid-template-rows: min-content;
    align-items: center;
    justify-items: center;
    justify-content: center;
    align-content: center;
  }
}
.base-modal .modal-container .instagram-modal .instagram-video-container {
  position: initial;
}
.base-modal .modal-container .instagram-modal .instagram-video-container + .instagram-modal_text {
  margin: -3px 0 0 0;
}
.base-modal .modal-container .instagram-modal .instagram-modal_text {
  background-color: white;
  padding: 10px 25px;
  font-family: 'Noto Sans JP', sans-serif;
  overflow-y: auto;
}
@media (min-width: 481px) {
  .base-modal .modal-container .instagram-modal .instagram-modal_text {
    height: 0;
    min-height: 100%;
    padding: 0 25px;
  }
}
.base-modal .modal-container .base-image-modal {
  height: 85vh;
  max-width: 940px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.base-modal .modal-container .base-image-modal .base-image {
  max-height: 85vh;
  object-fit: contain;
  max-width: 940px;
}
.main-header {
  width: 100%;
  height: 100vh;
}
.main-header .main-header_gradient {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&0+0,1+100 */
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 55%, #ffffff 100%);
  /* FF3.6-15 */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(255, 255, 255, 0)), color-stop(100%, #ffffff));
  /* Chrome4-9,Safari4-5 */
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 55%, #ffffff 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: -o-linear-gradient(top, rgba(255, 255, 255, 0) 55%, #ffffff 100%);
  /* Opera 11.10-11.50 */
  background: -ms-linear-gradient(top, rgba(255, 255, 255, 0) 55%, #ffffff 100%);
  /* IE10 preview */
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 55%, #ffffff 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  width: 100%;
  position: absolute;
  height: 100vh;
}
.main-header .main-header_image {
  width: 100%;
  height: 100vh;
  background-position-y: top;
  background-position-x: center;
  background-size: cover;
}
.main-footer {
  margin: 40px;
}
.main-footer .main-footer_sns {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8em;
}
.main-footer .main-footer_sns .insta-follow-btn {
  width: 13px;
  height: 13px;
  padding: 10px;
}
.main-footer .main-footer_disclaimer {
  font-size: 12px;
  text-align: center;
}
.hr {
  width: 100%;
}
.loading-screen {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  background-color: white;
  z-index: 9999;
  display: grid;
  grid-template-areas: 'a';
  align-items: center;
  justify-items: center;
  align-content: center;
  justify-content: center;
}
.loading-screen img {
  width: 70%;
  max-width: 500px;
  height: auto;
  opacity: 0.85;
}
.loading-screen .sign,
.loading-screen .texture {
  grid-area: a;
}
/**************/
/* ANIMATIONS */
/**************/
.fade-loadingscreen-enter-active,
.fade-loadingscreen-leave-active {
  transition: opacity 1s ease;
}
.fade-loadingscreen-enter-from,
.fade-loadingscreen-leave-to {
  opacity: 0;
}
.entrance-animation {
  animation: cardEntrance 700ms 0s ease-out;
  animation-fill-mode: backwards;
}
@keyframes cardEntrance {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.standard-fade-enter-active,
.standard-fade-leave-active {
  transition: opacity 0.25s ease;
}
.standard-fade-enter-from,
.standard-fade-leave-to {
  opacity: 0;
}
.standard-long-fade-enter-active,
.standard-long-fade-leave-active {
  transition: opacity 1.5s ease;
}
.standard-long-fade-enter-from,
.standard-long-fade-leave-to {
  opacity: 0;
}
.header-fade-enter-active,
.header-fade-leave-active {
  transition: opacity 1s ease;
}
.header-fade-enter-from,
.header-fade-leave-to {
  opacity: 0;
}
.title-animation-enter-active {
  animation: slide-in 1.5s;
}
.title-animation-leave-active {
  animation: slide-in 1.5s;
}
@keyframes slide-in {
  0% {
    transform: translateX(-40px);
    opacity: 0;
  }
  100% {
    transform: translateX(0px);
    opacity: 1;
  }
}
.work-card-animation-enter-active {
  animation: slide-down 1.5s;
}
.work-card-animation-leave-active {
  animation: slide-down 1.5s;
}
@keyframes slide-down {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
